import React from 'react'

// Styled-Components
import { Navbar, StyledNavLink, NavLogo, Logo } from './Navigation.styles'

const activeClassName = 'navLink-active'

const Navigation = ({ routes, logo }) => {
  console.log('Log @ Navigation', routes, logo)
  return (
    <Navbar>
      <NavLogo to="/">
        <Logo src={logo} alt="pnwtz logo" />
      </NavLogo>
      <div style={{ width: 'auto' }}>
        {routes.map(route => (
          <StyledNavLink key={route.name} exact to={route.path} activeClassName={activeClassName}>
            {route.name}
          </StyledNavLink>
        ))}
      </div>
    </Navbar>
  )
}

export default Navigation



