import React, { Component } from 'react'
import ErrorComponent from './../../components/ErrorComponent/ErrorComponent'

import Avatar from './../../assets/avatar.jpg'

class Portfolio extends Component {
  state = { errorOccurred: false }

  componentDidCatch = (error, info) => {
    console.log('Error occurred @ Portfolio: ', error, info)
    this.setState({ errorOccurred: true })
  }

  render() {
    if (this.state.errorOccurred) return <ErrorComponent bgColor="#334466" textColor="#eef2f6" />
    return (
      <div
        style={{
          width: '100vw',
          height: '100vh',
          backgroundColor: '#263859',
          display: 'flex',
          flexDirection: 'column',
          justifyContent: 'center',
          alignItems: 'center',
        }}
      >
        <div
          style={{
            display: 'flex',
            flexDirection: 'row',
            alignItems: 'center',
            marginBottom: '2rem',
          }}
        >
          <div
            style={{
              width: '8rem',
              height: '8rem',
              borderRadius: '100%',
              overflow: 'hidden',
              display: 'flex',
              justifyContent: 'center',
              alignItems: 'center',
              boxShadow: '0 2px 5px 0 #1e2d47',
            }}
          >
            <img style={{ height: '100%' }} src={Avatar} alt="It's me!" />
          </div>
          <div style={{ display: 'flex', flexDirection: 'column', marginLeft: '1rem' }}>
            <h2>Chris Pannwitz</h2>
            <p style={{ color: '#EEF2F6A8' }}>Frontend-Developer / Webdesigner</p>
          </div>
        </div>
        <h1>Work in progress.</h1>
        <a
          href="mailto:ch.von.pa@googlemail.com"
          style={{
            backgroundColor: '#CC4733',
            padding: '0.75rem 1.5rem',
            borderRadius: '2px',
            boxShadow: '0 1px 5px 0 #26385973',
            fontSize: '80%',
            fontFamily: 'sans-serif',
            fontWeight: 'bold',
            color: '#EBF2FF',
            textTransform: 'uppercase',
            textDecoration: 'none',
          }}
        >
          Contact me
        </a>
      </div>
    )
  }
}

export default Portfolio



