import React, { Fragment } from 'react'
import { BrowserRouter as Router, Route, Switch } from 'react-router-dom'

// Main navigation
import Navigation from './navigation/Navigation'
// Route components
import Portfolio from './portfolio/Portfolio'
import Blog from './blog/Blog'
import Projects from './projects/Projects'

// Main logo
import logo from './../assets/logo-pnwtz-white.svg'

const routeDefinitions = [
  { name: 'Portfolio', path: '/', component: Portfolio },
  { name: 'Blog', path: '/blog', component: Blog },
  { name: 'Projects', path: '/projects', component: Projects },
]

const Routes = () => {
  return (
    // basename: fix for GH-pages having problems with BrowserRouter, see https://github.com/facebook/create-react-app/issues/1765
    <Fragment>
      <Router basename={process.env.PUBLIC_URL}>
        <Fragment>
          <Navigation routes={routeDefinitions} logo={logo} />
          <Switch>
            {routeDefinitions.map(route => (
              <Route exact key={route.name} path={route.path} component={route.component} />
            ))}
          </Switch>
        </Fragment>
      </Router>
    </Fragment>
  )
}

export default Routes



