import React from 'react'
import PropTypes from 'prop-types'
import IconGram from './../IconGram/IconGram'

// Styled-Components
import { ErrorContainer, ErrorTextContainer, ErrorText } from './ErrorComponent.styles'

const ErrorComponent = ({
  bgColor = 'transparent',
  textColor = '#333',
  errorText = 'Something went wrong.',
}) => {
  console.log('textcolor', textColor)
  return (
    <ErrorContainer bgColor={bgColor}>
      <ErrorTextContainer>
        <IconGram icon="error-standard" color={textColor} />
        <ErrorText color={textColor}>{errorText}</ErrorText>
      </ErrorTextContainer>
    </ErrorContainer>
  )
}

ErrorComponent.propTypes = {
  bgColor: PropTypes.string,
  textColor: PropTypes.string,
  errorText: PropTypes.string,
}

export default ErrorComponent



