import React from 'react'
import PropTypes from 'prop-types'

// https://icongr.am/
const IconGram = ({ icon = 'error-standard', color = '#ffffff', size = 20 }) => {
  console.log('@icon: ', icon, color, size)
  return (
    <img
      src={`https://icongr.am/clarity/${icon}.svg?size=${size}&color=${color.replace('#', '')}`}
      alt=""
    />
  )
}

IconGram.propTypes = {
  icon: PropTypes.string.isRequired,
  size: PropTypes.number,
  color: PropTypes.string,
  set: PropTypes.string,
}

export default IconGram



