import styled from 'styled-components'
import { Link, NavLink } from 'react-router-dom'
import globalStyles from './../../index.styles'

const activeClassName = 'navLink-active'

export const Navbar = styled.nav`
  position: fixed;
  width: 100%;
  display: flex;
  flex-direction: row;
  flex-wrap: nowrap;
  justify-content: space-between;
  align-items: center;
  padding: ${globalStyles.spacing.ov} ${globalStyles.spacing.oh};
`

export const NavLogo = styled(Link)`
  width: 50%;
  max-width: 8rem;
  display: flex;
  flex-direction: row;
  flex-wrap: nowrap;
  justify-content: center;
  align-items: center;

  &:hover,
  &:active {
    opacity: 0.9;
  }
`

export const Logo = styled.img`
  width: 100%;
`

export const StyledNavLink = styled(NavLink).attrs({
  activeClassName,
})`
  color: ${globalStyles.colors.white.base};
  text-decoration: none;
  padding: ${globalStyles.spacing.iv} ${globalStyles.spacing.ih};
  margin: ${globalStyles.spacing.iv} ${globalStyles.spacing.ih};
  position: relative;
  &:before,
  &:after {
    content: '';
    position: absolute;
    bottom: 0;
    width: 0;
    height: 1px;
    background-color: ${globalStyles.colors.red.transparent.dark};
    transition: ${globalStyles.transition};
  }

  &:before {
    left: 0;
    background-color: ${globalStyles.colors.red.base};
  }
  &:after {
    right: 0;
    background-color: ${globalStyles.colors.red.base};
  }

  &.${activeClassName}, &:hover,
  &:active {
    &:before,
    &:after {
      width: 50%;
    }
  }
`



