import { injectGlobal } from 'styled-components'
import styledNormalize from 'styled-normalize'

/* Base variables */
const globalStyles = {
  transition: 'all .3s ease',
  spacing: {
    iv: '0.5rem',
    ih: '1rem',
    ov: '2rem',
    oh: '4rem',
  },
  colors: {
    white: {
      light: '#ffffff',
      base: '#eef2f6',
      dark: '#dde5ed',
      transparent: {
        light: '#EEF2F654',
        dark: '#EEF2F6A8',
      },
    },
    black: {
      lighter: '#78788b',
      light: '#545462',
      base: '#313139',
      dark: '#25252b',
      darker: '#19191e',
      transparent: {
        light: '#31313954',
        dark: '#313139A8',
      },
    },
    blue: {
      lighter: '#4565a0',
      light: '#354e7d',
      base: '#263859',
      dark: '#1e2d47',
      darker: '#172235',
      transparent: {
        light: '#26385954',
        dark: '#263859A8',
      },
    },
    red: {
      lighter: '#e96c61',
      light: '#e6574a',
      base: '#e34234',
      dark: '#df2e1f',
      darker: '#c8291c',
      transparent: {
        light: '#E3423454',
        dark: '#E34234A8',
      },
    },
  },
}

export default globalStyles

injectGlobal`
  ${styledNormalize}

  /* Import main font from google */
  @import url('https://fonts.googleapis.com/css?family=Assistant:300,400,600');

  /* Base styles */
  html {
    box-sizing: border-box;
  }
  body {
    font-family: 'Assistant', sans-serif;
    font-weight: 300;
    line-height: 1.45;
    color: ${globalStyles.colors.white.base};
    padding: 0;
  }

  *, ::before, ::after {
    box-sizing: inherit;
  }

  a {
    color: #eef2f6;
  }


  /* Font Scale */
  h1, h2, h3, h4 {
    /* margin: 1.414em 0 0.5em; */
    margin: 0 0 0.5rem 0;
    font-weight: 600;
    line-height: 1.2;
    letter-spacing: -1px;
    text-shadow: 1px 1px ${globalStyles.colors.blue.dark};
  }
  h1 {
    margin-top: 0;
    font-size: 2.441em;
    letter-spacing: -2px;
  }
  h2 {font-size: 1.953em;}
  h3 {font-size: 1.563em;}
  h4 {font-size: 1.25em;}
  small {font-size: 0.8em;}
  p {margin: 0 0 0.5rem 0;}
`



